@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap');

.card {
    background: linear-gradient(114.99deg, #471CA9 -0.99%, #5736A3 54.28%, #4B2A99 78.99%);
    box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

width: 380px;
position: relative;
}

.card__messageImg {
    padding: 28px 36px 18px 36px;
}

.avatar::after {
    width: 80px;
    height: 80px;
    content: " ";
    display: block;

    background: #EBD8FF;
    box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06), inset 0px -2.19582px 4.39163px #AE7BE3, inset 0px 4.39163px 3.29372px #FBF8FF;
    border-radius: 100%;

    position: absolute;
    left: 150px;
    bottom: -36px;
}

.avatar {
    display: block;
    content: " ";
    height: 7px;
    width: 380px;
    position: relative;          

    box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06), inset 0px -2.19582px 4.39163px #AE7BE3, inset 0px 4.39163px 3.29372px #FBF8FF;
    background-color: #EBD8FF;
}

.avatar>img {
    position: absolute;
    bottom: -27px;
    left: 159px;
    z-index: 100;

    border-radius: 100%;
}

.card__Tweets {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 16px;

    margin-top: 62px;
    margin-bottom: 26px;

    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;

    color: #EBD8FF;
}

.card__btn {
    display: flex;
    justify-content: center;
    padding-bottom: 36px;
}

.btn {
    width: 196px;
    height: 50px;

    background: #EBD8FF;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: #373737;
}

.is-active {
    background-color: #5CD3A8;
}

.goit__Span {
    position: absolute;
    top:20px;
    left: 20px;
}